.homePage {
    background-image: url('../../../public/home_page_photo.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size:cover ;
    text-align: center;
    align-items: center;
    height: 100%;
}


.homePage .nameOfCompany {
    font-size: 6rem;    
    transition: all ease-in-out 0.5s;
}

.homePage .ParagraphHomePag{
    font-size: 1rem;
    width: 20rem;
    font-weight: bold;
    transition: all ease-in-out 0.5s;
}

@media screen  and (max-width: 600px) {
    .homePage .nameOfCompany {
        padding-top: 1rem;
        font-size: 5rem;
    }

    .homePage .ParagraphHomePag{
        font-size: 1rem;
        width: 13rem;
        font-weight: bold;
    }

}